.btn {
	display: inline-block;
	height: 40px;
	padding: 0 8px;
	margin: 0 2px;
	font-size: 18px;
	font-weight: bold;
	background: transparent;
	-webkit-box-shadow: 0 0 0 3px #fff inset;
	        box-shadow: 0 0 0 3px #fff inset;
	cursor: pointer;
	color: #fff;
	-webkit-transition: background .3s;
	-o-transition: background .3s;
	transition: background .3s;
}



