body {
    background-attachment: fixed;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(56, 76, 97)), color-stop(85%, rgb(5, 7, 9)));
    background-image: -webkit-linear-gradient(rgb(56, 76, 97), rgb(5, 7, 9) 85%);
    background-image: -o-linear-gradient(rgb(56, 76, 97), rgb(5, 7, 9) 85%);
    background-image: linear-gradient(rgb(56, 76, 97), rgb(5, 7, 9) 85%);
    font-family: Helvetica, Arial, sans-serif;
}

* {
    padding: 0;
    margin: 0;
    border: none;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently */
    text-decoration: none;
    outline: none;
}



